body{
  direction: ltr;
  margin: 0px;
  padding: 0px;
}@media print {
  .item-details-container {
    direction: ltr; /* Maintain left-to-right layout when printing */
    display: flex;
    flex-direction: row;
  }
}
div{
  margin: 0;
  padding: 0;
}
span{
  margin: 0;
  padding: 0;
}
.logout-button {
  position: absolute;
  top: 20px;
  right: 20px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  font-size: 24px;
  color: #f44336;
  transition: transform 0.2s ease-in-out;
}

.logout-button:hover {
  transform: scale(1.2);
}

.logout-button::before {
  content: '\f011'; /* Unicode for power icon */
  font-family: 'Font Awesome 5 Free'; /* Ensure you include FontAwesome in your project */
  font-weight: 900; /* To get solid icon style */
}

.warning-popup {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(255, 255, 0, 0.9);
  padding: 20px;
  border: 2px solid #f44336;
  z-index: 1000;
  border-radius: 10px;
  font-size: 18px;
  color: #333;
}

.close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
  color: #f44336;
}
@media only screen and (max-width: 600px) {
  .app {
    padding: 10px;
  }

  .login-form {
    max-width: 90%;
    margin-top: 20px;
    padding: 15px;
  }

  .logout-button {
    top: 5px;
    margin-bottom: 15px;
    left:20px;

    font-size: 14px;
  }

  .item-details-form input,
  .item-details-form select {
    font-size: 14px;
  }

  .warning-popup {
    bottom: 10px;
    right: 10px;
    padding: 10px;
  }

  /* Card container */
  .card-container {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }

  .card {
    width: 100%;
    padding: 10px;
    margin: 10px;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  }

  .card h3 {
    font-size: 16px;
  }
  .item-details-form {
    margin-top: 30px;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
    background-color: #f9f9f9;
  }
  
  
  
}